const NodeRSA = require('node-rsa');
const sha256 = require('sha256')

const privateKeyData = `-----BEGIN PRIVATE KEY-----

-----END PRIVATE KEY-----`

const privateKey = new NodeRSA(privateKeyData, 'pkcs8', {
  environment: 'browser'
});

const sign = (data) => privateKey.encryptPrivate(sha256(data), 'base64');

const publicKeyData = `-----BEGIN PUBLIC KEY-----
MIGfMA0GCSqGSIb3DQEBAQUAA4GNADCBiQKBgQCgFGVfrY4jQSoZQWWygZ83roKXWD4YeT2x2p41dGkPixe73rT2IW04glagN2vgoZoHuOPqa5and6kAmK2ujmCHu6D1auJhE2tXP+yLkpSiYMQucDKmCsWMnW9XlC5K7OSL77TXXcfvTvyZcjObEz6LIBRzs6+FqpFbUO9SJEfh6wIDAQAB
-----END PUBLIC KEY-----`

const publicKey = new NodeRSA(publicKeyData, 'pkcs8-public', {
  environment: 'browser'
});

const verity = (data, sign) => {
  const dataHash = sha256(data);
  const signHash = publicKey.decryptPublic(new Buffer(sign, 'base64')).toString();
  return dataHash === signHash
}

module.exports = {
  sign,
  verity
}



